<template>
    <div class="app" @click="onClick">
        <div class="header">
            <img
                v-if="app.icon_uri && !iconFailedToLoad"
                class="icon"
                :src="app.icon_uri"
                @error="iconFailedToLoad = true"
            />
            <div v-else class="icon icon_stub"></div>

            <div class="info">
                <div class="line name">
                  <button class="filterable" @click="updateFilters('name')">{{ app.name }}</button>
                  v. {{ app.version }},
                  <button class="filterable ellipsis" @click="updateFilters('developer_name')">{{ app.developer_name }}</button>
                </div>

                <div v-if="app.lead" class="line ellipsis">{{ app.lead }}</div>

                <div v-if="app.is_compatibility_unknown" class="line">{{ $t('COMPATIBILITY_UNKNOWN_1') }}</div>
                <i18next
                  v-else-if="compatibleOperatingSystems.length"
                  tag="div"
                  path="FOR_X"
                  class="line ellipsis"
                >
                  <span>
                    <span
                      v-for="os in compatibleOperatingSystems"
                      class="compatible-device"
                      :key="os.name"
                    >
                      <button class="filterable" @click="updateFilters({ compatibility: os.name })">{{ os.device }}</button>
                    </span>
                  </span>
                </i18next>
            </div>
        </div>

        <div class="details">
            <div class="line">
              {{ $t('SOURCE') }}:
              <button class="filterable ellipsis" @click="updateFilters('source_name')">{{ app.source_name }}</button>
            </div>
            <div class="line">
              <button class="filterable ellipsis" @click="updateFilters('type')">{{ $t(app.type?.toUpperCase()) }}</button>
            </div>
            <div v-if="app.genre_id" class="line">
              <button class="filterable ellipsis" @click="updateFilters('genre_id')">{{ genre }}</button>
            </div>
        </div>

        <div v-if="customAction" class="actions">
            <!-- <v-button
              v-if="!customAction"
              type="primary"
              :loading="isInstalling"
              @click.native="install"
            >
              {{ $t('INSTALL') }}
            </v-button> -->
            <v-button
              v-if="customAction"
              type="primary"
              @click.native="$emit('custom-action-triggered', app)"
            >
              {{ customActionLabel }}
            </v-button>

            <!-- <v-button v-if="shareable">{{ $t('SHARE') }}</v-button> -->
        </div>
    </div>
</template>

<script>
import VButton from '@/components/Button.vue';
import { mapState } from 'vuex';
import { operatingSystems } from '@/data';
import installApp from '@/mixins/installApp';

export default {
  mixins: [installApp],
  props: {
    app: Object,
    customAction: Boolean,
    customActionLabel: String,
    shareable: Boolean,
  },
  components: {
    VButton,
  },
  data() {
    return {
      iconFailedToLoad: false,
      isInstalling: false,
    };
  },
  computed: {
    ...mapState(['genres']),
    genre() {
      return Object.values(this.genres.official).find(genre => genre.id === this.app.genre_id)?.name;
    },
    compatibleOperatingSystems() {
      return operatingSystems.filter(({ name }) => this.app[`min_${name}_version`]);
    },
  },
  methods: {
    updateFilters(filter) {
      if (typeof filter === 'string') {
        this.$emit('update-filters', { [filter]: this.app[filter] });
        return;
      }

      this.$emit('update-filters', filter);
    },
    onClick(e) {
      if (e.target.closest('button')) {
        return;
      }

      this.$router.push(`/details/${this.app.universal_object_identifier}`);
    },
    async install() {
      if (this.app.no_installation_ticket_failure_reason) {
        this.$notify({
          type: 'error',
          text: this.app.no_installation_ticket_failure_reason.translated,
        });
        return;
      }

      this.isInstalling = true;
      try {
        await this.installApp(this.app.object.id, 'universal', { installationTicket: this.app.installation_ticket });
      } finally {
        this.isInstalling = false;
      }
    },
  },
};
</script>

