}

.icon {
    flex: 0 0 57px;
    height: 57px;
    width: 57px;
    border-radius: 22.5%;
    mask: url(../../assets/ios-mask.svg) center/100% 100% no-repeat;
    object-fit: cover;
    object-position: center;

    &_stub {
        background-color: var(--stub-bg);
    }
}

.header,
.info,
.details {
    min-width: 0;
}

.header {
    display: flex;
    gap: 15px;
    flex-grow: 1;
}

.name {
    font-size: 16px;
}

.info {
    display: flex;
    flex-direction: column;
    gap: 3px;
}

.compatible-device {
  &:not(:last-child):after {
    content: ', '
  }
}

.details {
    display: flex;
    flex-direction: column;
