<template>
    <div class="search">
        <universal-filters v-model="search.filters" class="search__filters" :save-to-url="saveFiltersToUrl" />

        <div class="search__instruction">
          <slot name="instruction" />
        </div>

        <p v-if="!search.result.length && !search.isSearching">{{ $t('NOTHING_WAS_FOUND') }}</p>

        <div class="search__result" v-infinite-scroll="loadNextSearchPage">
            <universal-app
                v-for="app in search.result"
                :app="app"
                :custom-action="appCustomAction"
                :custom-action-label="appCustomActionLabel"
                @custom-action-triggered="$emit('app-custom-action-triggered', $event)"
                :shareable="appShareable"
                @update-filters="updateFilters"
                :key="app.universal_object_identifier"
            />
        </div>

        <v-loader
          v-if="!search.pagination.isLastPage"
          class="search__loading-spinner"
          :class="{
            'search__loading-spinner_visible': search.isSearching
          }"
        />
    </div>
</template>

<script>
import infiniteScroll from '@/mixins/infiniteScroll';
import UniversalFilters from './UniversalFilters.vue';
import UniversalApp from './UniversalApp.vue';

export default {
  mixins: [infiniteScroll],
  components: { UniversalFilters, UniversalApp },
  props: {
    appCustomAction: Boolean,
    appCustomActionLabel: String,
    appShareable: Boolean,
    saveFiltersToUrl: Boolean,
  },
  data() {
    return {
      search: {
        apiMethod: 'searchIndex',
        filters: {},
      },
    };
  },
  computed: {
    title() {
      return this.$t('SEARCH');
    },
  },
  methods: {
    updateFilters(filters) {
      this.$set(this.search, 'filters', {
        ...this.filters,
        ...filters,
      });
    },
  },
};
</script>

<style lang="scss" scoped>
.search {
    &__result {
        display: flex;
        flex-direction: column;
        align-items: stretch;
        gap: 15px;
        margin-top: 15px;
    }

    &__instruction,
    &__loading-spinner {
      margin-top: 15px;
    }

    &__loading-spinner {
      opacity: 0;

      &_visible {
        opacity: 1;
      }
    }
}
</style>
